html,
body {
  box-sizing:border-box;
  -moz-box-sizing:border-box; /* Firefox */
  -webkit-box-sizing:border-box; /* Safari */
}

div {
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.draggable_panel {
  border: 1px solid #777777;
  height: 100%;
}

.draggable_header {
 cursor: move;
 height: 40px;
 width: 100%;
}
