.menu_bar {
    width: 1280px;
    margin:0 auto;
    height: 60px;
}

.small_menu_bar {
  display: none;
}

.advimg {
  width: 1280px;
  margin:0 auto;
  height: 50px;
  display: block;
}

.iconbtns {
    float: right;
    margin-right: 5px;
    margin-top: 25px;
}

.cwbtn {
    float: right;
    margin-right: 0px;
    margin-top: 15px;
}

@media screen and (max-width: 1280px) {

  .menu_bar {
    width: 100%;
  }

  .advimg {
    width: 100%;
  }

}

@media screen and (max-width: 880px) {

  .menu_bar {
    display: none;
  }

  .small_menu_bar {
    display: block;
  }

  .advimg {
    width: 100%;
  }

}
